import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Coins, MapPin, Loader2 } from "lucide-react";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { ShopItem, usePurchaseItem } from "@/hooks/useShopItems";
import { useUserAddresses } from "@/hooks/useUserAddresses";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";

interface PurchaseConfirmDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  item: ShopItem;
  userPoints: number;
}

export const PurchaseConfirmDialog = ({
  open,
  onOpenChange,
  item,
  userPoints,
}: PurchaseConfirmDialogProps) => {
  const { t } = useTranslation();
  const { getLocalizedField } = useLocalizedContent();
  const { data: addresses = [] } = useUserAddresses();
  const purchaseMutation = usePurchaseItem();
  
  const [selectedAddressId, setSelectedAddressId] = useState<string>("");
  
  const isPhysical = item.item_type === 'physical';
  const remainingPoints = userPoints - item.points_cost;
  
  const handleConfirm = async () => {
    await purchaseMutation.mutateAsync({
      itemId: item.id,
      shippingAddressId: isPhysical ? selectedAddressId : undefined,
    });
    onOpenChange(false);
  };

  const canConfirm = !isPhysical || selectedAddressId;

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle>{t('shop.confirmPurchase')}</DialogTitle>
          <DialogDescription>
            {t('shop.confirmPurchaseDesc')}
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-4 py-4">
          {/* Item Info */}
          <div className="flex items-start gap-4 p-4 bg-secondary/30 rounded-lg">
            {item.image_url && (
              <img 
                src={item.image_url} 
                alt={String(getLocalizedField(item, 'name'))}
                className="w-16 h-16 rounded-lg object-cover"
              />
            )}
            <div className="flex-1">
              <h4 className="font-semibold">{getLocalizedField(item, 'name')}</h4>
              <p className="text-sm text-muted-foreground line-clamp-2">
                {getLocalizedField(item, 'description')}
              </p>
            </div>
          </div>

          {/* Points Info */}
          <div className="space-y-2">
            <div className="flex justify-between items-center">
              <span className="text-muted-foreground">{t('shop.currentPoints')}</span>
              <span className="flex items-center gap-1">
                <Coins className="w-4 h-4 text-accent" />
                <span className="font-medium">{userPoints}</span>
              </span>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-muted-foreground">{t('shop.pointsCost')}</span>
              <span className="flex items-center gap-1 text-destructive">
                <span>-</span>
                <Coins className="w-4 h-4" />
                <span className="font-medium">{item.points_cost}</span>
              </span>
            </div>
            <div className="border-t pt-2 flex justify-between items-center">
              <span className="font-medium">{t('shop.afterPurchase')}</span>
              <span className="flex items-center gap-1">
                <Coins className="w-4 h-4 text-accent" />
                <span className="font-bold">{remainingPoints}</span>
              </span>
            </div>
          </div>

          {/* Shipping Address (for physical items) */}
          {isPhysical && (
            <div className="space-y-2">
              <Label className="flex items-center gap-2">
                <MapPin className="w-4 h-4" />
                {t('shop.selectAddress')}
              </Label>
              {addresses.length === 0 ? (
                <p className="text-sm text-muted-foreground">
                  {t('shop.noAddresses')}
                </p>
              ) : (
                <Select value={selectedAddressId} onValueChange={setSelectedAddressId}>
                  <SelectTrigger>
                    <SelectValue placeholder={t('shop.selectAddressPlaceholder')} />
                  </SelectTrigger>
                  <SelectContent>
                    {addresses.map((address) => (
                      <SelectItem key={address.id} value={address.id}>
                        <div className="text-left">
                          <div className="font-medium">{address.label}</div>
                          <div className="text-sm text-muted-foreground">
                            {address.street}, {address.city}
                          </div>
                        </div>
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              )}
            </div>
          )}
        </div>

        <DialogFooter className="gap-2 sm:gap-0">
          <Button variant="outline" onClick={() => onOpenChange(false)}>
            {t('common.cancel')}
          </Button>
          <Button 
            onClick={handleConfirm} 
            disabled={!canConfirm || purchaseMutation.isPending}
          >
            {purchaseMutation.isPending ? (
              <>
                <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                {t('common.processing')}
              </>
            ) : (
              t('shop.confirmPurchaseBtn')
            )}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
};
