import { useState, useCallback } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { Coins, Package, Ticket, Award, ShoppingBag } from "lucide-react";
import { useQueryClient } from "@tanstack/react-query";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { useAuth } from "@/contexts/AuthContext";
import { useUserPoints } from "@/hooks/usePoints";
import { useShopItems, useUserPurchaseLimits, ShopItem } from "@/hooks/useShopItems";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { useSiteSettings } from "@/hooks/useSiteSettings";
import { useUserRole } from "@/hooks/useUserRole";
import { PurchaseConfirmDialog } from "@/components/PurchaseConfirmDialog";
import { PointsHistoryDialog } from "@/components/PointsHistoryDialog";
import { PullToRefresh } from "@/components/PullToRefresh";
import FeatureDisabledPage from "@/components/FeatureDisabledPage";

const Shop = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const { user } = useAuth();
  const { getLocalizedField } = useLocalizedContent();
  const { settings, isLoading: settingsLoading } = useSiteSettings();
  const { isAdmin } = useUserRole();
  const queryClient = useQueryClient();
  
  const { data: points = 0, isLoading: pointsLoading } = useUserPoints();
  const { data: items = [], isLoading: itemsLoading } = useShopItems();
  const { data: purchaseLimits = [] } = useUserPurchaseLimits();
  
  const [selectedItem, setSelectedItem] = useState<ShopItem | null>(null);
  const [purchaseDialogOpen, setPurchaseDialogOpen] = useState(false);

  // Pull-to-refresh handler - clears cache and forces fresh data
  const handleRefresh = useCallback(async () => {
    // Clear caches first
    queryClient.removeQueries({ queryKey: ['shop-items'] });
    queryClient.removeQueries({ queryKey: ['user-points'] });
    queryClient.removeQueries({ queryKey: ['user-purchase-limits'] });
    
    // Refetch with fresh data
    await Promise.all([
      queryClient.invalidateQueries({ queryKey: ['shop-items'] }),
      queryClient.invalidateQueries({ queryKey: ['user-points'] }),
      queryClient.invalidateQueries({ queryKey: ['user-purchase-limits'] }),
    ]);
  }, [queryClient]);

  const getItemTypeIcon = (type: string) => {
    switch (type) {
      case 'coupon':
        return <Ticket className="w-5 h-5" />;
      case 'physical':
        return <Package className="w-5 h-5" />;
      case 'badge':
        return <Award className="w-5 h-5" />;
      default:
        return <ShoppingBag className="w-5 h-5" />;
    }
  };

  const getItemTypeLabel = (type: string) => {
    switch (type) {
      case 'coupon':
        return t('shop.itemType.coupon');
      case 'physical':
        return t('shop.itemType.physical');
      case 'badge':
        return t('shop.itemType.badge');
      default:
        return type;
    }
  };

  const getUserRemainingPurchases = (item: ShopItem) => {
    const limit = purchaseLimits.find(l => l.shop_item_id === item.id);
    if (!limit) return item.per_user_limit;
    
    // Check if period needs reset
    const periodStart = new Date(limit.period_start);
    const now = new Date();
    
    if (item.limit_refresh_period === 'daily' && periodStart.toDateString() !== now.toDateString()) {
      return item.per_user_limit;
    }
    if (item.limit_refresh_period === 'weekly') {
      const weekAgo = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
      if (periodStart < weekAgo) return item.per_user_limit;
    }
    if (item.limit_refresh_period === 'monthly') {
      if (periodStart.getMonth() !== now.getMonth() || periodStart.getFullYear() !== now.getFullYear()) {
        return item.per_user_limit;
      }
    }
    
    return Math.max(0, item.per_user_limit - limit.purchase_count);
  };

  const canPurchase = (item: ShopItem) => {
    if (!user) return false;
    if (points < item.points_cost) return false;
    if (item.stock !== null && item.stock <= 0) return false;
    if (getUserRemainingPurchases(item) <= 0) return false;
    return true;
  };

  const handlePurchaseClick = (item: ShopItem) => {
    if (!user) {
      navigate('/auth');
      return;
    }
    setSelectedItem(item);
    setPurchaseDialogOpen(true);
  };

  // Check if shop is disabled for non-admin users
  if (!settingsLoading && settings?.shop_enabled === false && !isAdmin) {
    return <FeatureDisabledPage />;
  }

  if (itemsLoading || settingsLoading) {
    return (
      <div className="min-h-screen bg-secondary/20 py-8 px-4">
        <div className="container mx-auto max-w-6xl">
          <div className="text-center">{t('common.loading')}</div>
        </div>
      </div>
    );
  }

  return (
    <PullToRefresh onRefresh={handleRefresh}>
    <div className="min-h-screen bg-secondary/20 py-4 md:py-8 px-3 md:px-4">
      <div className="container mx-auto max-w-6xl">
        {/* Header */}
        <div className="mb-6 md:mb-8">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
            <div>
              <h1 className="text-2xl md:text-3xl font-bold text-foreground mb-2">
                {t('shop.title')}
              </h1>
              <p className="text-muted-foreground">
                {t('shop.description')}
              </p>
            </div>
            
            {user && (
              <Card className="md:w-auto">
                <CardContent className="p-4 flex items-center gap-4">
                  <div className="flex items-center gap-2">
                    <Coins className="w-6 h-6 text-accent" />
                    <span className="text-2xl font-bold text-foreground">
                      {pointsLoading ? '...' : points}
                    </span>
                    <span className="text-muted-foreground">{t('shop.points')}</span>
                  </div>
                  <PointsHistoryDialog />
                </CardContent>
              </Card>
            )}
          </div>
        </div>

        {/* Items Grid */}
        {items.length === 0 ? (
          <Card>
            <CardContent className="py-12 text-center">
              <ShoppingBag className="w-12 h-12 mx-auto mb-4 text-muted-foreground" />
              <p className="text-muted-foreground">{t('shop.noItems')}</p>
            </CardContent>
          </Card>
        ) : (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 md:gap-6">
            {items.map((item) => {
              const isSoldOut = item.stock !== null && item.stock <= 0;
              const remainingPurchases = getUserRemainingPurchases(item);
              const isLimitReached = remainingPurchases <= 0;
              const insufficientPoints = points < item.points_cost;
              
              return (
                <Card 
                  key={item.id} 
                  className={`overflow-hidden ${isSoldOut ? 'opacity-60' : ''}`}
                >
                  {/* Image */}
                  {item.image_url && (
                    <div className="aspect-video overflow-hidden bg-secondary/30">
                      <img 
                        src={item.image_url} 
                        alt={String(getLocalizedField(item, 'name'))}
                        className="w-full h-full object-cover"
                      />
                    </div>
                  )}
                  
                  <CardHeader className="pb-2">
                    <div className="flex items-start justify-between gap-2">
                      <div className="flex-1">
                        <CardTitle className="text-lg line-clamp-1">
                          {getLocalizedField(item, 'name')}
                        </CardTitle>
                        <CardDescription className="line-clamp-2 mt-1">
                          {getLocalizedField(item, 'description')}
                        </CardDescription>
                      </div>
                      <Badge variant="secondary" className="flex items-center gap-1 shrink-0">
                        {getItemTypeIcon(item.item_type)}
                        <span className="hidden sm:inline">{getItemTypeLabel(item.item_type)}</span>
                      </Badge>
                    </div>
                  </CardHeader>
                  
                  <CardContent className="pt-0">
                    {/* Price and Stock */}
                    <div className="flex items-center justify-between mb-3">
                      <div className="flex items-center gap-1">
                        <Coins className="w-4 h-4 text-accent" />
                        <span className="font-bold text-lg">{item.points_cost}</span>
                        <span className="text-muted-foreground text-sm">{t('shop.points')}</span>
                      </div>
                      
                      <div className="text-sm text-muted-foreground">
                        {item.stock !== null ? (
                          isSoldOut ? (
                            <Badge variant="destructive">{t('shop.soldOut')}</Badge>
                          ) : (
                            <span>{t('shop.stock')}: {item.stock}</span>
                          )
                        ) : (
                          <span>{t('shop.unlimited')}</span>
                        )}
                      </div>
                    </div>
                    
                    {/* Limit Info */}
                    <div className="text-sm text-muted-foreground mb-3">
                      {item.limit_refresh_period === 'none' ? (
                        <span>{t('shop.limitTotal', { count: item.per_user_limit })}</span>
                      ) : (
                        <span>
                          {t(`shop.limit.${item.limit_refresh_period}`, { count: item.per_user_limit })}
                        </span>
                      )}
                      {user && !isSoldOut && (
                        <span className="ml-2">
                          ({t('shop.remaining')}: {remainingPurchases})
                        </span>
                      )}
                    </div>
                    
                    {/* Purchase Button */}
                    <Button 
                      className="w-full"
                      disabled={!canPurchase(item)}
                      onClick={() => handlePurchaseClick(item)}
                    >
                      {!user ? (
                        t('shop.loginToPurchase')
                      ) : isSoldOut ? (
                        t('shop.soldOut')
                      ) : isLimitReached ? (
                        t('shop.limitReached')
                      ) : insufficientPoints ? (
                        t('shop.insufficientPoints')
                      ) : (
                        t('shop.purchase')
                      )}
                    </Button>
                  </CardContent>
                </Card>
              );
            })}
          </div>
        )}
      </div>

      {/* Purchase Confirm Dialog */}
      {selectedItem && (
        <PurchaseConfirmDialog
          open={purchaseDialogOpen}
          onOpenChange={setPurchaseDialogOpen}
          item={selectedItem}
          userPoints={points}
        />
      )}
    </div>
    </PullToRefresh>
  );
};

export default Shop;
